import { useTera } from 'lib';
import { isString } from 'lodash';
import { Fragment, ReactElement, ReactNode, cloneElement } from 'react';
import { customTwMerge } from 'tailwind-merge.config';

export interface BreadcrumbsProps {
  items?: ItemType[];
  separator?: ReactNode;
  className?: string;
  containerItemClassName?: string;
}

export interface ItemType {
  title?: ReactElement | string;
  children?: ItemType[];
  onClick?: (e: any) => void;
}
const Breadcrumbs = ({
  items,
  separator,
  className,
  containerItemClassName,
  ...restProps
}: BreadcrumbsProps) => {
  const defaultClasses =
    'flex items-center gap-1 text-xxs italic dark:text-gray-400 dark:hover:text-white text-gray-800';
  const breadcrumbsItemClasses = customTwMerge(
    defaultClasses,
    containerItemClassName,
  );

  // function Dropdown(props: { items: ItemType }) {
  //   const { items } = props;
  //   const defaultClasses =
  //     'block px-4 py-2 hover:bg-gray-100 dark:hover:bg-gray-600 dark:hover:text-white';
  //   return (
  //     <div className="z-10 hidden bg-white divide-y divide-gray-100 rounded-lg shadow w-44 dark:bg-gray-700">
  //       <ul
  //         className="py-2 text-sm text-gray-700 dark:text-gray-200"
  //         aria-labelledby="dropdownDefault"
  //       >
  //         sdada
  //       </ul>
  //     </div>
  //   );
  // }

  const { is_verify } = useTera();
  if (!is_verify) return null;
  return (
    <ol
      className={customTwMerge('flex items-center', className)}
      aria-label="Breadcrumb"
      {...restProps}
    >
      {items.map((item, key) => (
        <Fragment key={key}>
          <li className={breadcrumbsItemClasses}>
            {key !== 0 && <span className="mx-2">{separator || '>'}</span>}
            {isString(item.title) ? (
              <span className="font-bold">{item.title}</span>
            ) : (
              cloneElement(item.title, {
                className: 'hover:text-blue-600 cursor-pointer font-normal',
              })
            )}
          </li>

          {/* <Dropdown items={item} /> */}
        </Fragment>
      ))}
    </ol>
  );
};

export default Breadcrumbs;
