import { useTera } from 'lib';
import {
  ForwardRefRenderFunction,
  createContext,
  forwardRef,
  useEffect,
  useState,
} from 'react';
import { customTwMerge } from 'tailwind-merge.config';
import Checkbox from './Checkbox';
import {
  CheckboxGroupContext,
  CheckboxGroupProps,
  CheckboxValueType,
} from './interface';

export const GroupContext = createContext<CheckboxGroupContext | null>(null);

const InternalCheckboxGroup: ForwardRefRenderFunction<
  HTMLDivElement,
  CheckboxGroupProps
> = (
  {
    defaultValue,
    children,
    options = [],
    className,
    inline,
    disabled,
    styles,
    // onChange,
    ...restProps
  },
  ref,
) => {
  const [value, setValue] = useState<CheckboxValueType[]>(
    restProps.value || defaultValue || [],
  );

  const defaultClasses = 'flex gap-1.5';
  const customClasses = customTwMerge(
    defaultClasses,
    className,
    inline ? 'flex-row' : 'flex-col',
  );

  useEffect(() => {
    if ('value' in restProps) {
      setValue(restProps.value || []);
    }
  }, [restProps.value]);

  const { is_verify } = useTera();
  if (!is_verify) return null;

  const getOptions = () =>
    options.map((option) => {
      if (typeof option === 'string' || typeof option === 'number') {
        return {
          label: option,
          value: option,
        };
      }
      return option;
    });

  if (options && options.length > 0) {
    children = getOptions().map((option) => (
      <Checkbox
        key={option.value.toString()}
        disabled={'disabled' in option ? option.disabled : disabled}
        value={option.value}
        defaultChecked={value.includes(option.value)}
        onChange={option.onChange}
        className={option.className}
        style={option.style}
        title={option.title}
      >
        {option.label}
      </Checkbox>
    ));
  }
  const context = {
    defaultValue,
    value,
    disabled,
    // onChange,
  };

  return (
    <div className={customClasses} ref={ref} style={styles}>
      <GroupContext.Provider value={context}>{children}</GroupContext.Provider>
    </div>
  );
};
const CheckboxGroup = forwardRef<HTMLDivElement, CheckboxGroupProps>(
  InternalCheckboxGroup,
);
export default CheckboxGroup;
