import { Dropdown, DropdownItem, EllipsisVerticalOutlined, useTera } from 'lib';
import useMergedState from 'rc-util/lib/hooks/useMergedState';
import {
  WheelEventHandler,
  useCallback,
  useEffect,
  useMemo,
  useRef,
  useState,
} from 'react';
import { customTwMerge } from 'tailwind-merge.config';
import TabPane from './TabPane';

export type TabsType = 'line' | 'card' | 'editable-card';
export type TabsPosition = 'top' | 'right' | 'bottom' | 'left';
export interface TabsProps {
  itemClassName?: string;
  className?: string;
  id?: string;
  items?: TabItemType[];
  defaultActiveKey?: string;
  activeKey?: string;
  onChange?: (activeKey: string) => void;
  onTabClick?: (
    activeKey: string,
    e: React.MouseEvent | React.KeyboardEvent,
  ) => void;
}

export interface TabItemType {
  key: string;
  label: React.ReactNode;
  disabled?: boolean;
  children?: React.ReactNode;
}
type CustomArray<T> = Record<string, T>;
const Tabs = ({
  items,
  defaultActiveKey,
  onChange,
  activeKey,
  // itemClassName,
  onTabClick,
  className,
}: TabsProps) => {
  const containerRef = useRef<HTMLDivElement>(null);
  const containerItemRef = useRef<HTMLDivElement>(null);
  const itemsRef = useRef<CustomArray<HTMLButtonElement>>({});
  const containerEl = containerRef?.current;
  const containerItemEl = containerItemRef?.current;
  const [dropdownItems, setDropdownItems] = useState([]);
  const [currentTab, setCurrentTab] = useMergedState('', {
    value: activeKey,
    onChange: (v) => onChange?.(v),
    defaultValue: defaultActiveKey || items?.[0]?.key,
  });
  const [dividerStyle, setDividerStyle] = useState({
    left: 0,
    width: 0,
  });
  const isItemsOverWidth = useMemo(
    () => containerItemEl?.offsetWidth > containerEl?.offsetWidth,
    [containerItemEl, containerEl],
  );

  const positionScrollRef = useRef(0);
  const [positionScroll, setPositionScroll] = useState(0);

  const setStyleDivider = useCallback(() => {
    const itemNode = itemsRef.current[currentTab];

    const positionItem =
      itemNode?.getBoundingClientRect()?.left -
      containerItemEl?.getBoundingClientRect().left;
    if (currentTab) {
      setDividerStyle({
        left: positionItem || 0,
        width: itemNode?.clientWidth,
      });
    }
  }, [containerItemEl, containerItemRef, currentTab]);

  useEffect(
    () => setStyleDivider(),
    [containerItemEl, containerItemRef, currentTab],
  );

  const onInternalTabClick = (
    key: string,
    e: React.MouseEvent | React.KeyboardEvent,
  ) => {
    setCurrentTab(key);
    onTabClick?.(key, e);
  };

  const onWheel: WheelEventHandler<HTMLDivElement> = (e) => {
    if (isItemsOverWidth) {
      const { deltaY } = e;

      const positionScrollEnd =
        containerItemEl.offsetWidth - containerEl.offsetWidth;
      positionScrollRef.current += deltaY;

      if (positionScrollRef.current <= 0) {
        positionScrollRef.current = 0;
      }
      if (positionScrollRef.current >= positionScrollEnd) {
        positionScrollRef.current = positionScrollEnd;
      }
      setPositionScroll(positionScrollRef.current);
    }
  };

  useEffect(() => {
    if (containerItemEl) {
      Object.assign(containerItemEl.style, {
        transform: `translate(-${positionScroll}px, 0px)`,
      });
    }
  }, [positionScroll]);

  const onScrollIntoViewTab = useCallback(
    (currentTab: string) => {
      if (itemsRef && containerItemEl && containerEl) {
        const itemNode = itemsRef.current[currentTab];
        let positionScrollIntoItem = 0;
        const positionItem =
          itemNode?.getBoundingClientRect().left -
          containerItemEl.getBoundingClientRect().left;
        if (positionItem + itemNode?.offsetWidth > containerEl?.offsetWidth) {
          positionScrollIntoItem =
            positionItem -
            containerEl?.offsetWidth +
            itemNode.getBoundingClientRect()?.width;
        } else {
          positionScrollIntoItem = positionItem;
        }

        setPositionScroll(positionScrollIntoItem);
        positionScrollRef.current = positionScrollIntoItem;
      }
    },
    [itemsRef, containerItemEl, containerEl],
  );

  const changeItemDropdown = (item: DropdownItem) => {
    const keyTab = String(item.key);
    setCurrentTab(keyTab);
    onScrollIntoViewTab(keyTab);
  };

  const isTabIntoView = useMemo(
    () => (keyTab: string) => {
      if (containerEl && itemsRef.current) {
        const positionItem: number =
          itemsRef?.current[keyTab]?.getBoundingClientRect().left -
          containerEl.getBoundingClientRect().left;
        const leftIntoView = positionItem >= 0;
        const rightIntoView =
          positionItem + itemsRef.current[keyTab]?.offsetWidth <=
          containerEl.offsetWidth;
        return leftIntoView && rightIntoView;
      }
    },
    [itemsRef.current, containerEl],
  );

  useEffect(() => {
    if (!isTabIntoView(currentTab)) {
      onScrollIntoViewTab(currentTab);
    }
  }, [currentTab, itemsRef.current, containerEl]);

  useEffect(() => {
    if (containerEl && itemsRef.current) {
      const arr = items?.filter((item) => {
        if (isTabIntoView(item.key)) return;

        return {
          key: item.key,
          label: item.label,
          disabled: item.disabled,
        };
      });
      setDropdownItems(arr);
    }
  }, [items, positionScroll, containerEl, itemsRef.current]);

  const { is_verify } = useTera();
  if (!is_verify) return null;

  const listItemHeader = () => {
    return (
      <div
        className="relative flex self-stretch flex-auto overflow-auto translate-x-0 translate-y-0 whitespace-nowrap scroll-smooth no-scrollbar"
        ref={containerRef}
      >
        <div
          className="flex gap-2 text-sm font-medium text-center"
          onWheel={onWheel}
          role="tablist"
          ref={containerItemRef}
        >
          {items?.map((tab) => {
            return (
              <TabPane
                {...tab}
                keyTab={tab.key}
                ref={(el) => (itemsRef.current[tab.key] = el)}
                activeTab={currentTab === tab.key}
                onClick={(e) => onInternalTabClick(tab.key, e)}
              />
            );
          })}
          <div
            className="absolute bottom-0 h-0.5 bg-blue-600 transition-all"
            style={dividerStyle}
          />
        </div>
      </div>
    );
  };

  const btnOperationClasses = customTwMerge(
    'relative self-stretch p-3 bg-white before:shadow-[-3px_0px_4px_-1px_rgba(0,0,0,0.25)] before:absolute before:transition-opacity before:opacity-100 before:pointer-events-none before:left-0 before:w-1 before:inset-y-0 before:bg-white',
  );
  return (
    <div
      className={customTwMerge(
        'relative flex items-center flex-none mb-5 border-b border-gray-200 dark:border-gray-700 w-full',
        className,
      )}
    >
      {listItemHeader()}

      {isItemsOverWidth && (
        <Dropdown
          menu={{
            items: dropdownItems,
            selectable: true,
            selectedKeys: currentTab,
            onChangeItem: changeItemDropdown,
          }}
        >
          <button type="button" className={btnOperationClasses}>
            <EllipsisVerticalOutlined className="w-5" />
          </button>
        </Dropdown>
      )}
    </div>
  );
};

if (process.env.NODE_ENV !== 'production') {
  Tabs.displayName = 'Tabs';
}

export default Tabs;
