import { useHover, useOnClickOutside } from 'hooks';
import { EyeOutlined } from 'icons';
import { useTera } from 'lib';
import { useRef, useState } from 'react';
import { createPortal } from 'react-dom';
import { customTwMerge } from 'tailwind-merge.config';

export interface ImageProps {
  src?: string;
  width?: number;
  alt?: string;
  borderRadius?: number;
  imageClassName?: string;
  containerClassName?: string;
  preview?: { src: string };
}
const Image = ({
  src,
  width,
  alt,
  preview,
  borderRadius,
  imageClassName,
  containerClassName,
  ...restProps
}: ImageProps) => {
  const [openPreview, setOpenPreview] = useState(false);
  const imgRef = useRef<HTMLDivElement>(null);
  const previewImgRef = useRef<HTMLImageElement>(null);
  const { isHover } = useHover(imgRef);

  const handleOpen = () => {
    setOpenPreview(true);
    document.body.className = 'overflow-hidden';
  };
  const handleClose = () => {
    setOpenPreview(false);
    document.body.className = '';
  };

  useOnClickOutside(previewImgRef, handleClose);

  const { is_verify } = useTera();
  if (!is_verify) return null;

  const previewImage = (
    <div className="preview-image">
      <div className="fixed inset-0 z-[100] h-full backdrop-brightness-50" />
      <div className="fixed inset-0 overflow-auto outline-none z-[101]">
        <div className="absolute inset-0 flex items-center justify-center">
          <img
            src={preview ? preview.src : src}
            ref={previewImgRef}
            alt={alt}
            className="max-w-full max-h-[70%] align-middle cursor-grab select-none transition-transform"
          />
        </div>
      </div>
    </div>
  );

  const maskImage = (
    <div
      style={{ borderRadius }}
      className={customTwMerge(
        'absolute inset-0 flex items-center justify-center transition-opacity text-white cursor-pointer backdrop-brightness-50 opacity-0',
        isHover && 'opacity-100',
      )}
    >
      <div
        className={customTwMerge(
          'p-1 overflow-hidden whitespace-nowrap text-ellipsis text-xxs',
          !isHover && 'hidden',
        )}
      >
        <span className="inline-block align-middle me-1">
          <EyeOutlined className="w-4 h-4" />
        </span>
        Preview
      </div>
    </div>
  );
  return (
    <>
      <div
        style={{ width: width }}
        className={customTwMerge('relative inline-block', containerClassName)}
        ref={imgRef}
        onClick={handleOpen}
        {...restProps}
      >
        <div
          style={{ borderRadius, width, backgroundImage: `url(${src})` }}
          className={customTwMerge('bg-cover bg-no-repeat', imageClassName)}
        >
          <img
            src={src}
            className={customTwMerge('invisible', imageClassName)}
          />
        </div>
        {maskImage}
      </div>
      {openPreview && createPortal(previewImage, document.body)}
    </>
  );
};
if (process.env.NODE_ENV !== 'production') {
  Image.displayName = 'Image';
}

export default Image;
