import { useTera } from 'lib';
import React, { Key, forwardRef } from 'react';
import { customTwMerge } from 'tailwind-merge.config';

export interface TabPaneProps {
  keyTab?: Key;
  activeTab?: boolean;
  label?: React.ReactNode;
  disabled?: boolean;
  className?: string;
  onClick?: (event: any) => void;
}
const TabPane = forwardRef<HTMLButtonElement, TabPaneProps>(
  ({ label, disabled, onClick, activeTab, className, keyTab }, ref) => {
    const defaultClasses =
      'relative inline-block p-4 dark:hover:text-gray-300 disabled:text-gray-300 disabled:cursor-not-allowed hover:text-blue-600 outline-0 border-0';

    const customClasses = customTwMerge(
      defaultClasses,
      className,
      activeTab ? 'text-blue-600' : 'text-gray-500',
    );

    const { is_verify } = useTera();
    if (!is_verify) return null;

    return (
      <button
        ref={ref}
        className={customClasses}
        type="button"
        role="tab"
        data-node-key={keyTab}
        onClick={onClick}
        disabled={disabled}
      >
        {label}
      </button>
    );
  },
);

export default TabPane;
