import { ChevronDownOutlined, ChevronUpOutlined } from 'icons';
import React, {
  ChangeEvent,
  FocusEventHandler,
  forwardRef,
  useEffect,
  useRef,
  useState,
} from 'react';
import { customTwMerge } from 'tailwind-merge.config';
import Input, { InputProps } from '.';
import { isNil } from 'lodash';
import { useTera } from 'lib';
export interface InputNumberProps
  extends Omit<InputProps, 'onChange' | 'type'> {
  value?: number;
  onPressEnter?: React.KeyboardEventHandler<HTMLInputElement>;
  onChange?: (value: number | ChangeEvent<Element>) => void;
  min?: number;
  max?: number;
}
const InputNumber = forwardRef<HTMLInputElement, InputNumberProps>(
  (props, ref) => {
    const {
      className,
      value: customValue,
      onChange,
      onPressEnter,
      onBlur,
      onKeyDown,
      disabled,
      min,
      max,
      ...restProps
    } = props;

    const previousValue = useRef<number>(null);
    const [value, setValue] = useState<string | number>(null);
    const inputClasses = customTwMerge(`pr-9`, className);

    const updateValue = (value: number) => {
      previousValue.current = value;
      setValue(value);
    };

    useEffect(() => {
      updateValue(customValue);
    }, [customValue]);

    const formatValue = (paramValue: string) => {
      const parseNumberValue = parseFloat(paramValue);
      let formatValue: number = null;

      if (isNil(paramValue)) {
        formatValue = null;
      } else if (isNaN(parseNumberValue)) {
        formatValue = previousValue.current;
      } else if (parseNumberValue < min) {
        formatValue = min;
      } else if (parseNumberValue > max) {
        formatValue = max;
      } else if (paramValue === '-0') {
        formatValue = 0;
      } else {
        formatValue = parseNumberValue;
      }
      return formatValue;
    };

    const plusNumber = () => {
      if (+value + 1 > max) {
        return;
      }
      if (!disabled) {
        updateValue(+value + 1);
        onChange?.(+value + 1);
      }
    };
    const minusNumber = () => {
      if (+value - 1 < min) {
        return;
      }
      if (!disabled) {
        updateValue(+value - 1);
        onChange?.(+value - 1);
      }
    };

    const handleChange = (e: ChangeEvent<HTMLInputElement>) => {
      setValue(e.target.value);
    };

    const handleBlur: FocusEventHandler<HTMLInputElement> = (e) => {
      onBlur?.(e);
      updateValue(formatValue(e.target.value));
      if (previousValue.current !== customValue) {
        onChange?.(formatValue(e.target.value));
      }
    };

    const handleKeydown: React.KeyboardEventHandler<HTMLInputElement> = (e) => {
      onKeyDown?.(e);
      const { key } = e;
      if (key === 'Enter') {
        updateValue(formatValue(e.currentTarget.value));
        if (previousValue.current !== customValue) {
          onChange?.(formatValue(e.currentTarget.value));
        }
        onPressEnter?.(e);
      }
      if (['Up', 'ArrowUp'].includes(key)) {
        plusNumber();
        e.preventDefault();
      }
      if (['Down', 'ArrowDown'].includes(key)) {
        minusNumber();
        e.preventDefault();
      }
    };

    const { is_verify } = useTera();
    if (!is_verify) return null;

    return (
      <div className="relative">
        <Input
          ref={ref}
          onBlur={handleBlur}
          onChange={handleChange}
          className={inputClasses}
          disabled={disabled}
          value={value ?? ''}
          onKeyDown={handleKeydown}
          {...restProps}
        />
        <div className="absolute inset-y-0 right-0 flex flex-col items-stretch h-full transition-all border border-transparent w-9 rounded-se-xsm rounded-ee-xsm">
          <button
            disabled={disabled}
            className="flex items-center justify-center flex-auto text-gray-400 cursor-pointer h-2/5 hover:text-gray-900 disabled:cursor-not-allowed disabled:hover:text-gray-400"
            type="button"
            onClick={plusNumber}
          >
            <ChevronUpOutlined className="w-4 h-4 stroke-[2px]" />
          </button>
          <button
            disabled={disabled}
            className="flex items-center justify-center flex-auto text-gray-400 cursor-pointer h-2/5 hover:text-gray-900 disabled:cursor-not-allowed disabled:hover:text-gray-400"
            type="button"
            onClick={minusNumber}
          >
            <ChevronDownOutlined className="w-4 h-4 stroke-[2px]" />
          </button>
        </div>
      </div>
    );
  },
);
export default InputNumber;
