import React, {
  ReactElement,
  ReactNode,
  Ref,
  cloneElement,
  useEffect,
  useRef,
  useState,
} from 'react';
import { createPortal } from 'react-dom';
import CSSMotion from 'rc-motion';
import {
  Placement,
  arrow,
  autoUpdate,
  offset,
  shift,
  useFloating,
  flip,
} from '@floating-ui/react-dom';
import { customTwMerge } from 'tailwind-merge.config';
import { composeRef, supportRef } from 'rc-util/lib/ref';
import { useHover, useOnClickOutside } from 'hooks';
import { useTera } from 'lib';

export interface PopoverProps {
  content?: ReactNode;
  title?: ReactNode;
  children?: ReactElement;
  placement?: Placement;
  trigger?: 'hover' | 'focus' | 'click';
  className?: string;
  open?: boolean;
  onOpenChange?: (open: boolean) => void;
}
const Popover = ({
  children,
  placement: customPlacement = 'top',
  onOpenChange,
  open,
  title,
  trigger = 'hover',
  className,
  content,
  ...restProps
}: PopoverProps) => {
  const refArrow = useRef<HTMLDivElement>(null);
  const arrowEl = refArrow.current;
  const { refs, floatingStyles, placement, middlewareData } =
    useFloating<HTMLElement>({
      placement: customPlacement,
      whileElementsMounted: autoUpdate,
      middleware: [
        offset(5),
        flip(),
        shift(),
        arrow({
          element: refArrow,
        }),
      ],
    });
  const { isHover } = useHover(refs.reference);
  const [isOpen, setIsOpen] = useState(false);
  const [arrowStyles, setArrowStyles] = useState({});

  useEffect(() => {
    if (trigger === 'hover') {
      return setIsOpen(isHover);
    }
  }, [trigger, isHover]);

  useEffect(() => {
    open && setIsOpen(open);
  }, [open]);

  useEffect(() => {
    onOpenChange?.(isOpen);
  }, [isOpen]);

  useEffect(() => {
    if (!arrowEl) {
      return;
    }
    const arrowWidth = arrowEl.offsetWidth;

    const side = placement.split('-');

    const staticSide = {
      top: 'bottom',
      right: 'left',
      bottom: 'top',
      left: 'right',
    }[side[0]];

    if (middlewareData.arrow) {
      const { x, y } = middlewareData.arrow;
      let customX = {};
      if (side[1] === 'start') {
        customX = { left: '12px' };
      } else if (side[1] === 'end') {
        customX = { right: '12px' };
      } else {
        customX = { left: `${x}px` };
      }
      setArrowStyles({
        ...customX,
        top: y != null ? `${y}px` : '',
        [staticSide]: `${-arrowWidth / 2}px`,
        transform: 'rotate(45deg)',
      });
    }
  }, [middlewareData]);

  const popoverClasses = customTwMerge(
    'inline-block p-3 text-gray-800 bg-white shadow-md rounded-md text-xxs dark:bg-gray-700 z-[90]',
    className,
  );
  const handleOpenPopover = (e) => {
    if (trigger === 'click') {
      children?.props?.onClick?.(e);
      setIsOpen(!isOpen);
    }
    if (trigger === 'focus') {
      children?.props?.onFocus?.(e);
      setIsOpen(true);
    }
  };

  useOnClickOutside([refs.floating, refs.reference], () => setIsOpen(false));

  const { is_verify } = useTera();
  if (!is_verify) return null;

  const nodePopover = (
    <CSSMotion visible={isOpen} motionName="opacity-motion">
      {({ className }) => (
        <div
          style={floatingStyles}
          role="tooltip"
          ref={refs.setFloating}
          className={customTwMerge(popoverClasses, className)}
        >
          {title && (
            <div className="min-w-[177px] mb-2 font-semibold">{title}</div>
          )}
          {content && <div>{content}</div>}
          <div
            className="absolute w-2 h-2 pointer-events-none bg-inherit"
            ref={refArrow}
            style={arrowStyles}
          />
        </div>
      )}
    </CSSMotion>
  );

  const renderChildren = cloneElement(children, {
    ref: supportRef(children)
      ? composeRef(
          refs.setReference,
          (children as ReactElement & { ref: Ref<HTMLElement> }).ref,
        )
      : undefined,
    onClick: (e) => handleOpenPopover(e),
    onFocus: (e) => handleOpenPopover(e),
    ...restProps,
  });

  return (
    <>
      {renderChildren}
      {createPortal(nodePopover, document.body)}
    </>
  );
};

export default Popover;
