import { throttle } from 'lodash';
import { useEffect, useState } from 'react';

export const WIDTH_SIZES = {
  TABLET: 768,
  DESKTOP: 1024,
};

export const useDetectDevice = () => {
  const [isMobile, setIsMobile] = useState<boolean>(false);
  const [isTablet, setIsTablet] = useState<boolean>(false);
  const [isDesktop, setIsDesktop] = useState<boolean>(false);
  const [widthScreen, setWidthScreen] = useState<number>(window.innerWidth);

  useEffect(() => {
    const handleResize = throttle(() => {
      setWidthScreen(window.outerWidth);
      if (window.outerWidth < WIDTH_SIZES.TABLET) {
        setIsMobile(true);
        setIsTablet(false);
        setIsDesktop(false);
      } else if (
        window.outerWidth >= WIDTH_SIZES.TABLET &&
        window.outerWidth < WIDTH_SIZES.DESKTOP
      ) {
        setIsMobile(false);
        setIsTablet(true);
        setIsDesktop(false);
      } else if (window.outerWidth >= WIDTH_SIZES.DESKTOP) {
        setIsMobile(false);
        setIsTablet(false);
        setIsDesktop(true);
      }
    }, 1000);

    handleResize();
    window.addEventListener('resize', handleResize);

    return () => {
      window.removeEventListener('resize', handleResize);
    };
  }, []);

  return {
    isMobile,
    isTablet,
    isDesktop,
    widthScreen,
  };
};
