import { ModalProps } from './interface';
import Button from '../Button';
import { XMarkOutlined } from 'icons';

interface FooterProps {
  onOk?: React.MouseEventHandler<HTMLButtonElement | HTMLAnchorElement>;
  onCancel?: React.MouseEventHandler<HTMLButtonElement | HTMLAnchorElement>;
}
interface HeaderProps {
  title: React.ReactNode;
  closeIcon?: React.ReactNode;
  onCancel?: React.MouseEventHandler<HTMLButtonElement | HTMLAnchorElement>;
}
export const Footer: React.FC<
  FooterProps &
    Pick<
      ModalProps,
      | 'footer'
      | 'okText'
      | 'okType'
      | 'cancelText'
      | 'confirmLoading'
      | 'okButtonProps'
      | 'cancelButtonProps'
    >
> = (props) => {
  const {
    okText = 'Ok',
    okType = 'primary',
    cancelText = 'Cancel',
    confirmLoading,
    onOk,
    onCancel,
    okButtonProps,
    cancelButtonProps,
  } = props;

  return (
    <>
      <Button onClick={onCancel} type="primary" outlined {...cancelButtonProps}>
        {cancelText}
      </Button>
      <Button
        loading={confirmLoading}
        onClick={onOk}
        type={okType}
        {...okButtonProps}
      >
        {okText}
      </Button>
    </>
  );
};

export const Header: React.FC<HeaderProps> = ({
  title,
  closeIcon = <XMarkOutlined className="w-5 h-5" />,
  onCancel,
}) => {
  return (
    <div className="flex items-start justify-between rounded-t dark:border-gray-600">
      {title && (
        <h3 className="w-full text-xl font-medium text-gray-900 uppercase dark:text-white">
          {title}
        </h3>
      )}
      {closeIcon && (
        <button
          onClick={onCancel}
          type="button"
          className="inline-flex items-center p-1 ml-auto text-sm text-black bg-transparent rounded-lg hover:bg-gray-200 hover:text-gray-900 dark:hover:bg-gray-600 dark:hover:text-white"
        >
          {closeIcon}
          <span className="sr-only">Close modal</span>
        </button>
      )}
    </div>
  );
};
