import { itemStyles } from 'common/const/formStyle';
import { DivProps } from 'common/interfaces';
import { FormItemContext } from 'components/Form/FormItem';
import { useTera } from 'components/TeraProvider';
import React, { ReactNode, forwardRef, useContext } from 'react';
import { customTwMerge } from 'tailwind-merge.config';
export interface InputProps
  extends Omit<
    React.DetailedHTMLProps<
      React.InputHTMLAttributes<HTMLInputElement>,
      HTMLInputElement
    >,
    'ref' | 'prefix'
  > {
  prefix?: ReactNode;
  suffix?: ReactNode;
  prefixProps?: {
    onClick?: React.MouseEventHandler<HTMLDivElement>;
    className?: string;
  };
  suffixProps?: {
    onClick?: React.MouseEventHandler<HTMLDivElement>;
    className?: string;
  };
  containerProps?: DivProps;
}

const Input = forwardRef<HTMLInputElement, InputProps>((props, ref) => {
  const formItemContext = useContext(FormItemContext);
  const {
    className,
    id,
    prefix,
    suffix,
    prefixProps,
    suffixProps,
    containerProps,
    ...restProps
  } = props;
  const { is_verify } = useTera();
  if (!is_verify) return null;

  const inputStyles =
    'border border-gray-200 rounded-xsm px-4 py-2.5 text-xxs placeholder:text-gray-400 focus-visible:outline-none w-full transition-all outline-none';

  const customClasses = customTwMerge(
    inputStyles,
    itemStyles.disabled,
    formItemContext?.isError ? itemStyles.error : itemStyles.primary,
    suffix && 'pr-9',
    prefix && 'pl-9',
    className,
  );

  const baseInput = (
    <input
      ref={ref}
      autoComplete="confirm-password"
      id={id || formItemContext?.name}
      className={customClasses}
      {...restProps}
    />
  );

  const customInput = (
    <div className="relative" {...containerProps}>
      {prefix && (
        <div
          className={customTwMerge(
            'absolute inset-y-0 left-0 flex items-center pl-4',
            prefixProps?.className,
          )}
          onClick={prefixProps?.onClick}
        >
          {prefix}
        </div>
      )}
      {baseInput}
      {suffix && (
        <div
          className={customTwMerge(
            'absolute inset-y-0 right-0 flex items-center pr-4',
            suffixProps?.className,
          )}
          onClick={suffixProps?.onClick}
        >
          {suffix}
        </div>
      )}
    </div>
  );

  return <>{prefix || suffix ? customInput : baseInput}</>;
});

export { default as InputNumber } from './InputNumber';
export type { InputNumberProps } from './InputNumber';
export { default as InputPassword } from './InputPassword';
export type { InputPasswordProps, VisibilityToggle } from './InputPassword';
export { default as Search } from './Search';
export type { SearchProps } from './Search';
export default Input;
