import Dialog from 'rc-dialog';
import React from 'react';
import { ModalProps } from './interface';
import { Footer, Header } from './shared';
import { customTwMerge } from 'tailwind-merge.config';
import { useTera } from 'lib';
const Modal: React.FC<ModalProps> = (props) => {
  const {
    className,
    open,
    closeIcon,
    width,
    footer,
    title,
    children,
    centered = true,
    wrapClassName,
    ...restProps
  } = props;

  const handleCancel = (e: React.MouseEvent<HTMLButtonElement>) => {
    const { onCancel } = props;
    onCancel?.(e);
  };

  const handleOk = (e: React.MouseEvent<HTMLButtonElement>) => {
    const { onOk } = props;
    onOk?.(e);
  };

  const dialogFooter =
    footer === undefined ? (
      <Footer {...restProps} onOk={handleOk} onCancel={handleCancel} />
    ) : (
      footer
    );

  const wrapClasses = customTwMerge(
    centered && 'text-center tera-modal-centered',
    wrapClassName,
  );

  const { is_verify } = useTera();
  if (!is_verify) return null;

  return (
    <Dialog
      {...restProps}
      width={width}
      prefixCls="tera-modal"
      footer={dialogFooter}
      visible={open}
      closable={false}
      onClose={handleCancel}
      wrapClassName={wrapClasses}
      className={className}
      title={
        <Header title={title} closeIcon={closeIcon} onCancel={handleCancel} />
      }
    >
      {children}
    </Dialog>
  );
};

export default Modal;
