import Checkbox from 'components/Checkbox/Checkbox';
import { ChevronDownOutlined, Dropdown, Radio } from 'lib';
import { findIndex, isEqual, uniqWith } from 'lodash';
import { GetRowKey } from 'rc-table/lib/interface';
import useMergedState from 'rc-util/lib/hooks/useMergedState';
import { ChangeEvent, useCallback, useEffect, useMemo, useState } from 'react';
import { updateToggleObjectInArr } from 'utils/function';
import {
  AnyObject,
  ColumnsType,
  SelectionItem,
  TableRowSelection,
} from '../interface';

interface UseSelectionConfig<RecordType extends AnyObject = AnyObject> {
  data: RecordType[];
  getRowKey?: GetRowKey<RecordType>;
}

export const SELECTION_COLUMN = {} as const;
export const SELECTION_ALL = 'SELECT_ALL' as const;
export const SELECTION_INVERT = 'SELECT_INVERT' as const;
export const SELECTION_NONE = 'SELECT_NONE' as const;

export type INTERNAL_SELECTION_ITEM = SelectionItem;

const useSelection = <RecordType extends AnyObject = AnyObject>(
  config: UseSelectionConfig,
  rowSelection?: TableRowSelection<RecordType>,
) => {
  const {
    defaultSelectedRowKeys,
    getCheckboxProps,
    onChange,
    selections,
    selectedRowKeys,
    type = 'checkbox',
  } = rowSelection || {};
  const { data: dataDisplay, getRowKey } = config || {};
  const [mergedSelectedKeys, setMergedSelectedKeys] = useMergedState(
    selectedRowKeys || defaultSelectedRowKeys || [],
    {
      value: selectedRowKeys,
    },
  );
  const [selectedRows, setSelectedRows] = useState<RecordType[]>([]);

  const recordKeys = dataDisplay.map(getRowKey);
  const checkedCurrentAll = recordKeys.every((key) =>
    mergedSelectedKeys?.includes(key),
  );

  const checkedCurrentSome = recordKeys.some((key) =>
    mergedSelectedKeys?.includes(key),
  );

  // Reset if rowSelection reset
  useEffect(() => {
    if (!rowSelection) {
      setSelectedRows([]);
      setMergedSelectedKeys([]);
    }
  }, [!!rowSelection]);

  useEffect(() => setSelectedKeys(), [selectedRows]);

  useEffect(() => {
    if (!!defaultSelectedRowKeys) {
      setSelectedRows(
        dataDisplay.filter((item) =>
          defaultSelectedRowKeys.includes(getRowKey(item)),
        ),
      );
    }
  }, [defaultSelectedRowKeys]);

  const setSelectedKeys = useCallback(() => {
    const selectedRowKeys = selectedRows.map((row) => getRowKey(row));
    setMergedSelectedKeys(selectedRowKeys);
    onChange?.(selectedRowKeys, selectedRows);
  }, [selectedRows]);

  const onCheckAllChange = (e: ChangeEvent<HTMLInputElement>) => {
    e.target.checked
      ? setSelectedRows(uniqWith([...selectedRows, ...dataDisplay], isEqual))
      : setSelectedRows(
          [...selectedRows].filter(
            (item) => !recordKeys.includes(getRowKey(item)),
          ),
        );
  };

  const triggerSingleSelection = useCallback(
    (record: RecordType) => {
      const indexItem = findIndex(
        selectedRows,
        (item) => getRowKey(item) === getRowKey(record),
      );
      if (type === 'checkbox') {
        setSelectedRows(
          updateToggleObjectInArr(indexItem, record, selectedRows),
        );
      } else {
        setSelectedRows([record]);
      }
    },
    [setSelectedKeys, selectedRows],
  );

  const dropdownSelection = (
    <Dropdown
      menu={{
        items: selections?.map((item, index) => ({
          key: item.key ?? index,
          label: item.text,
          onClick: () => item.onSelect(recordKeys),
        })),
      }}
    >
      <span className="absolute w-4 h-4 text-gray-400 top-0 ms-[100%] ps-1 z-10 cursor-pointer">
        <ChevronDownOutlined className="stroke-2" />
      </span>
    </Dropdown>
  );
  const injectRowSelection = useMemo(
    () =>
      (columns: ColumnsType<RecordType>): ColumnsType<RecordType> => {
        if (rowSelection) {
          (columns || []).splice(0, 0, {
            title: type === 'checkbox' && (
              <div className="relative">
                <Checkbox
                  checked={!!dataDisplay.length && checkedCurrentAll}
                  indeterminate={!checkedCurrentAll && checkedCurrentSome}
                  containerClassName="justify-center"
                  onChange={onCheckAllChange}
                />
                {rowSelection.selections && dropdownSelection}
              </div>
            ),
            render: (_, record: RecordType) => {
              return (
                <>
                  {type === 'checkbox' ? (
                    <Checkbox
                      checked={mergedSelectedKeys.includes(getRowKey(record))}
                      containerClassName="justify-center"
                      onChange={() => {
                        triggerSingleSelection(record);
                      }}
                      {...getCheckboxProps?.(record)}
                    />
                  ) : (
                    <Radio
                      checked={mergedSelectedKeys.includes(getRowKey(record))}
                      onChange={() => {
                        triggerSingleSelection(record);
                      }}
                    />
                  )}
                </>
              );
            },
            width: 100,
          });
          return columns;
        } else {
          return columns;
        }
      },
    [mergedSelectedKeys, rowSelection, dataDisplay],
  );

  return injectRowSelection;
};
export default useSelection;
